.background {
  text-align: center;
  background-color: #004C5F;
  background-position: center;
  min-height: 500vh;
  padding-top: 5%;
  padding-bottom: 5%;
  background-image: url(./marble.jpg);
}
.header {
  margin-left: 10%;
  margin-right: 10%;
  position : center;
  top : 6px;
  align-items: center;
  font-size: calc(18px + 2vmin);
  color: black;
  font-family: monospace;
  }

  .border {
      border-style: outset;
      border: 2;
      color: black;
    }

  .topnav {
  overflow: hidden;
}

/* Navbar links */
.topnav a {
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

/* Links - change color on hover */
.topnav a:hover {
  background-color: #ddd;
  color: black;
}
